import { BlockMock, DeepPartial, RewardMock } from '../core'
import {
  VaultBlockEarnings,
  VaultBlockPerformance,
  VaultPerformance,
} from './vault-performance.model'

/**
 * Vault Performance Mock
 */
export function VaultPerformanceMock(
  options?: DeepPartial<VaultPerformance>
): VaultPerformance {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'VAULT_PERFORMANCE_ID',

    vaultId: options?.vaultId || 'VAULT_ID',
    vaultBlockId: options?.vaultBlockId || 'VAULT_BLOCK_ID',
    block: BlockMock(options?.block),
    age: options?.age || 0,
    holders: options?.holders || 0,
    realizedAPY: options?.realizedAPY || 0,
    earnings: VaultBlockEarningsMock(options?.earnings),
    accruedRewards: options?.accruedRewards?.map((a) => RewardMock(a)),

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}

export function VaultBlockEarningsMock(
  options?: Partial<VaultBlockEarnings>
): VaultBlockEarnings {
  return {
    token: options?.token || '0',
    USD: options?.USD || '0',
    percentage: options?.percentage || 0,
  }
}

export function VaultBlockPerformanceMock(
  options?: DeepPartial<VaultBlockPerformance>
): VaultBlockPerformance {
  return {
    age: options?.age || 0,
    holders: options?.holders || 0,
    realizedAPY: options?.realizedAPY || 0,
    earnings: VaultBlockEarningsMock(options?.earnings),
    accruedRewards: options?.accruedRewards?.map((a) => RewardMock(a)),
  }
}
